  <div class="container">
    <!-- <img src="@/assets/bg-2.svg" alt="" class="bg" /> -->
    <div class="code-box">
      <h2>𝕎𝕚𝕕𝕧𝕖𝕣𝕤𝕖 · 汪汪汪世界</h2>
      <div class="inner">
        <!-- <el-input
          prefix-icon="el-icon-lock"
