        ></el-input> -->
        <input
          :type="code_type"
          class="input-code"
          v-model="code"
          placeholder="请输入访问口令"
        />
        <i class="el-icon-view" @click="triggerCodeType" />
        <div class="code-btn" @click="checkCode">
          <i class="el-icon-right" color="#333333"></i>
        </div>
      </div>
      <br />
      <p class="msg">口令区分大小写</p>
