<script>
import md5 from "js-md5";
export default {
  data() {
    return {
      code: "",
      code_type: "password"
    };
  },
  mounted() {},
  methods: {
    checkCode() {
      const _this = this;
      if (!this.code)
        return this.$message({
          message: "请输入正确的访问口令",
          center: true,
          type: "error",
        });
      if (md5(this.code) === this.$token) {
        this.$message({ message: "访问成功", center: true, type: "success" });
        localStorage.setItem("token", md5(this.code));
        setTimeout(function () {
          _this.$router.push({ name: "lib" });
        }, 2000);
      } else {
        return this.$message({
          message: "口令错误",
          center: true,
          type: "error",
        });
      }
    },
    triggerCodeType() {
      let _t = this.code_type === "text" ? "password" : "text";
      this.code_type = _t;
    }
  },
};
</script>
